import React, { useContext, useState } from "react";
import {
  Typography,
  TextField,
  Button,
  Stepper,
  Step,
  StepLabel,
  Card,
  CardContent,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import Checkbox from '@mui/material/Checkbox';
import './form.css';
import { useAPI } from "../../hooks/useAPI";
import { AuthContext } from "../../contexts/Auth/AuthContext";


const useStyles = makeStyles( theme => ({
  button: {
    marginRight: '8px',
  },
}));

function getSteps() {
  return [
    "Identifica√ß√£o do projeto",
    "Inova√ß√£o",
    "Relevancia da Solu√ß√£o",
    "Possibilidade de investimento",
    "Impacto da solu√ß√£o",
    "Tempo de apresenta√ß√£o",
    "Dominio da apresenta√ß√£o",
    "Design",
    "Clareza do problema",
    "Clareza do mercado",
  ];
}


export default function LinaerStepper ({ dataProject, modalConfirmation, modalError }){

    const [notaInovacao, setInovacao] = useState("");
    const [comentarioInovacao, setComentarioInovacao] = useState('');
    const [relevancia, setNotaRelevancia] = useState('');
    const [comentariosRelevancia, setComentarioRelevancia] = useState('');
    const [investimento, setInvestimento] = useState('');
    const [comentarioInvestimento, setCommentInvestimento] = useState("");
    const [impacto, setimpacto] = useState('');
    const [commentImpacto, setComentarioImpacto] = useState('');
    const [tempo, settempo] = useState('');
    const [commenttempo, setCommentTempo] = useState('');
    const [dominio, setDominio] = useState('');
    const [commentdominio, setCommentDominio] = useState('');
    const [design, setDesign] = useState('');
    const [commentDesign, setCommentDesign] = useState('');
    const [clareza, setClareza] = useState('');
    const [commentClareza, setCommentClareza] = useState('');
    const [mercado, setMercado] = useState('');
    const [commentMercado, setCommentMercado] = useState('');
    const [eliminado, setEliminado] = useState('');

    const api = useAPI();
    const auth = useContext(AuthContext);



    const getStepContent = (step) => {

      const eliminacao = () => {
        setEliminado(!eliminado);
        console.log(eliminado);
      }
      
      switch (step) {
        case 0:

          return (
            <>
                <div className="contentStep0">
                    <div className="tituloProjeto">
                        <h2>Projeto: {dataProject['name']}</h2>
                    </div>
                    <div className="linkAcesso">
                      <p>Link de acesso: <a href={dataProject['link']}>{dataProject['link']}</a></p>
                    </div>
                    <div className="resumoProjeto">
                        <h2>Resumo</h2>
                        <p>{dataProject['resume']}</p>
                    </div>
                    <div className="eliminacao">
                        <Checkbox
                            checked={eliminado}
                            onChange={eliminacao}
                            inputProps={{ 'aria-label': 'controlled' }}
                            style={{'marginTop': '6px' }}
                        />
                        <p>Desclassificado por n√£o utilizar um ou mais recursos disponibilizados pelas agencias espaciais participantes do Space apps</p>
                    </div>
                </div>
            </>
          );
    
        case 1:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="notaInovacao"
                  label="Nota de Inova√ß√£o"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inovacaoNota"
                  margin="normal"
                  name="notaInovacao"
                  type="number"
                  value={notaInovacao}
                  onChange={(e) => setInovacao(e.target.value)}
                />
              </div>
              <TextField
                id="comentarioInovacao"
                label="Comentarios sobre inova√ß√£o"
                variant="outlined"
                placeholder="Comente sobre a inova√ß√£o no projeto"
                margin="normal"
                name="comentarioInovacao"
                className="comentarioInovacao"
                multiline
                value={comentarioInovacao}
                maxRows={10}
                onChange={(e) => setComentarioInovacao(e.target.value)}
              />
            </>
          );
        case 2:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="notaAplicabilidade"
                  label="Nota de Relevancia"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="notaAplicabilidade"
                  margin="normal"
                  name="notaAplicabilidade"
                  type="number"
                  value={relevancia}
                  onChange={(e) => setNotaRelevancia(e.target.value)}
                />
              </div>
              <TextField
                id="comentarioAplicabilidade"
                label="Comentarios sobre relevancia"
                variant="outlined"
                placeholder="Comente sobre a relevancia do projeto"
                margin="normal"
                name="comentarioAplicabilidade"
                className="comentarioAplicabilidade"
                multiline
                value={comentariosRelevancia}
                maxRows={10}
                onChange={(e) => setComentarioRelevancia(e.target.value)}
              />
            </>
          );
        case 3:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="viabilidadeEconomica"
                  label="Nota de possibilidade de investimentos"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="viabilidadeEconomica"
                  margin="normal"
                  name="viabilidadeEconomica"
                  type="number"
                  value={investimento}
                  onChange={(e) => setInvestimento(e.target.value)}
                />
              </div>
              <TextField
                id="comentarioEconomico"
                label="Comentarios sobre a possibilidade de investimentos"
                variant="outlined"
                placeholder="Comente sobre a possibilidade de investimentos"
                margin="normal"
                name="comentarioEconomico"
                className="comentarioEconomico"
                multiline
                value={comentarioInvestimento}
                maxRows={10}
                onChange={(e) => setCommentInvestimento(e.target.value)}
              />
            </>
        );
        case 4:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="sustentabilidade"
                  label="Nota de impacto"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="sustentabilidade"
                  margin="normal"
                  name="sustentabilidade"
                  type="number"
                  value={impacto}
                  onChange={(e) => setimpacto(e.target.value)}
                />
              </div>
              <TextField
                id="comentarioSustentabilidade"
                label="Comentarios sobre o impacto do projeto"
                variant="outlined"
                placeholder="Comente sobre o impacto do projeto"
                margin="normal"
                name="comentarioSustentabilidade"
                className="comentarioSustentabilidade"
                multiline
                value={commentImpacto}
                maxRows={10}
                onChange={(e) => setComentarioImpacto(e.target.value)}
              />
            </>
        );
        case 5:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="inclusao"
                  label="Nota sobre o tempo de apresenta√ß√£o"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inclusao"
                  margin="normal"
                  name="inclusao"
                  type="number"
                  value={tempo}
                  onChange={(e) => settempo(e.target.value)}
                />
              </div>
              <TextField
                id="commentarioInclusao"
                label="Comentarios sobre o tempo de apresenta√ß√£o da equipe"
                variant="outlined"
                placeholder="Comente sobre o tempo de apresenta√ß√£o da equipe"
                margin="normal"
                name="commentarioInclusao"
                className="commentarioInclusao"
                multiline
                value={commenttempo}
                maxRows={10}
                onChange={(e) => setCommentTempo(e.target.value)}
              />
            </>
        );
        case 6:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="inclusao"
                  label="Nota de Dominio"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inclusao"
                  margin="normal"
                  name="inclusao"
                  type="number"
                  value={dominio}
                  onChange={(e) => setDominio(e.target.value)}
                />
              </div>
              <TextField
                id="commentarioInclusao"
                label="Comentarios sobre o dominio do tema"
                variant="outlined"
                placeholder="Comente sobre o dominio do tema pela equipe"
                margin="normal"
                name="commentarioInclusao"
                className="commentarioInclusao"
                multiline
                value={commentdominio}
                maxRows={10}
                onChange={(e) => setCommentDominio(e.target.value)}
              />
            </>
        );
        case 7:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="inclusao"
                  label="Nota de Design"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inclusao"
                  margin="normal"
                  name="inclusao"
                  type="number"
                  value={design}
                  onChange={(e) => setDesign(e.target.value)}
                />
              </div>
              <TextField
                id="commentarioInclusao"
                label="Comentarios sobre o design da aplica√ß√£o"
                variant="outlined"
                placeholder="Comente sobre a o design do projeto"
                margin="normal"
                name="commentarioInclusao"
                className="commentarioInclusao"
                multiline
                value={commentDesign}
                maxRows={10}
                onChange={(e) => setCommentDesign(e.target.value)}
              />
            </>
        );
        case 8:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="inclusao"
                  label="Nota de clareza"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inclusao"
                  margin="normal"
                  name="inclusao"
                  type="number"
                  value={clareza}
                  onChange={(e) => setClareza(e.target.value)}
                />
              </div>
              <TextField
                id="commentarioInclusao"
                label="Comentarios sobre a clareza da solu√ß√£o"
                variant="outlined"
                placeholder="Comente sobre a clareza da solu√ß√£o"
                margin="normal"
                name="commentarioInclusao"
                className="commentarioInclusao"
                multiline
                value={commentClareza}
                maxRows={10}
                onChange={(e) => setCommentClareza(e.target.value)}
              />
            </>
        );
        case 9:
          return (
            <>
              <div className="enderecoDiv">
                <TextField
                  id="inclusao"
                  label="Nota de Mercado"
                  variant="outlined"
                  placeholder="Digite sua nota"
                  className="inclusao"
                  margin="normal"
                  name="inclusao"
                  type="number"
                  value={mercado}
                  onChange={(e) => setMercado(e.target.value)}
                />
              </div>
              <TextField
                id="commentarioInclusao"
                label="Comentarios sobre a clareza do nicho de mercado"
                variant="outlined"
                placeholder="Comente sobre a clareza do nicho de mercado do projeto"
                margin="normal"
                name="commentarioInclusao"
                className="commentarioInclusao"
                multiline
                value={commentMercado}
                maxRows={10}
                onChange={(e) => setCommentMercado(e.target.value)}
              />
            </>
        );
        default:
          return "A√á√ÉO DESCONHECIDA, ATUALIZE A P√ÅGINA, DESCULPE POR ISSO ";
      }
    }

    const classes = useStyles();
    const [activeStep, setActiveStep] = useState(0);
    const [skippedSteps, setSkippedSteps] = useState([]);
    const steps = getSteps();

  const isStepOptional = (step) => {
    return step === 1 || step === 2;
  };

  const isStepSkipped = (step) => {
    return skippedSteps.includes(step);
  };

  const handleNext = () => {

    setActiveStep(activeStep + 1);
    setSkippedSteps(skippedSteps.filter((skipItem) => skipItem !== activeStep));
    
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const handleSkip = () => {
    if (!isStepSkipped(activeStep)) {
      setSkippedSteps([...skippedSteps, activeStep]);
    }
    setActiveStep(activeStep + 1);
  };

  const handleEnviarRespostas = async() => {

    const response = await api.salvaNotasProjeto(dataProject['id'], auth.user['id'], notaInovacao, comentarioInovacao, relevancia, comentariosRelevancia, investimento, comentarioInvestimento, impacto, commentImpacto, tempo, commenttempo, dominio, commentdominio, design, commentDesign, clareza, commentClareza, mercado, commentMercado).then((response) => {
      return response;
    });

    if(response.length === 0){
      modalConfirmation();
    };
  };

  const handleDesclassificarProjeto = async() => {
    
    const response = await api.desclassificaProjeto(dataProject['id']);

    modalError();

  }

  return (
    <div>
        <Card className="cardFormIncidente">
            <Stepper alternativeLabel activeStep={activeStep} className="marcadorPasso">
                {steps.map((step, index) => {
                const labelProps = {};
                const stepProps = {};
                if (isStepOptional(index)) {
                    labelProps.optional = (
                    <Typography
                        variant="caption"
                        align="center"
                        style={{ display: "block" }}
                    >
                    </Typography>
                    );
                }
                if (isStepSkipped(index)) {
                    stepProps.completed = false;
                }
                return (
                    <Step {...stepProps} key={index}>
                    <StepLabel {...labelProps}>{step}</StepLabel>
                    </Step>
                );
                })}
            </Stepper>
            
            <CardContent>
                {activeStep === steps.length ? (
                    <Typography variant="h3" align="center">
                    Thank You
                    </Typography>
                ) : (
                    <>
                        <form className="formCadastroIncidente">{getStepContent(activeStep)}</form>

                        {!eliminado? <div className="cardIncidenteButton">
                            <Button
                              className={classes.button}
                              variant="contained"
                              color="primary"
                              disabled={activeStep === 0}
                              onClick={handleBack}
                              style={{marginRight: '10px', width: '100px'}}
                              >
                              Voltar
                            </Button>
                            {activeStep === steps.length - 1 ? <Button
                                className={classes.button}
                                variant="contained"
                                color="primary"
                                onClick={handleEnviarRespostas}
                                style={{marginRight: '10px', width: '100px'}}
                            >
                                Enviar
                            </Button> : <Button
                                className={classes.button}
                                variant="contained"
                                color="primary"
                                onClick={handleNext}
                                style={{marginRight: '10px', width: '100px'}}
                            >
                                Pr√≥ximo
                            </Button>}
                        </div> : <div className="cardIncidenteButton">
                            <Button
                              className={classes.button}
                              variant="contained"
                              color="error"
                              onClick={handleDesclassificarProjeto}
                              style={{marginRight: '10px', width: '150px'}}
                              >
                              Desclassificar
                            </Button>
                        </div> }
                    </> 
                )}
            </CardContent>
      </Card>
    </div>
  );
};