
export const useAPI = () => ({

    validateLogin: async(email: string, password: string) => {

        let url = `${process.env.REACT_APP_API}/application/authenticate`

        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({"username": email, "password": password}));

        let dados = JSON.parse(response.responseText)
        
        return dados;
    },
    logout: async () => {
        const response = "Deslogado";
        // const response = await api.post('/application/logout');
        return response;
    },
    buscaProjetosPorJurado: async(idJurado: number) => {
        
        let url = `${process.env.REACT_APP_API}/application/buscaProjetos/idJurado`
        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({"idjurado": idJurado}));

        let dados = JSON.parse(response.responseText);

        return dados;
    },
    salvaNotasProjeto: async(idprojeto: number, idAvaliador: number, notaInovacao: number, comentarioInovacao: string, relevancia: number, comentariosRelevancia: string, investimento: number, comentarioInvestimento: string, impacto: number, commentImpacto: string, tempo: number, commenttempo: string, dominio: number, commentdominio: string, design: number, commentDesign: string, clareza: number, commentClareza: string, mercado: number, commentMercado: string) => {

        let url = `${process.env.REACT_APP_API}/application/lancaNotas/avaliado`;
        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({
            "projeto": idprojeto,
            "jurado": idAvaliador,
            "notaInova": notaInovacao, 
            "commentInova": comentarioInovacao, 
            "notaRelev": relevancia, 
            "commentRelev": comentariosRelevancia, 
            "notaInvest": investimento, 
            "commentInvest": comentarioInvestimento, 
            "notaImpacto": impacto, 
            "commentImpacto": commentImpacto, 
            "notaTempo": tempo, 
            "commentTempo": commenttempo, 
            "dominio": dominio, 
            "commentDominio": commentdominio, 
            "design": design, 
            "commentDesign": commentDesign, 
            "clareza": clareza, 
            "commentClareza": commentClareza, 
            "mercado": mercado, 
            "commentMercado": commentMercado
        }));

        let dados = JSON.parse(response.responseText);

        return dados;
    },
    desclassificaProjeto: async(idProjeto: number) => {
        
        let url = `${process.env.REACT_APP_API}/application/desclassifica/projeto`;
        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({
            "idProjeto": idProjeto
        }));

        let dados = JSON.parse(response.responseText);

        return dados;
    },
    solicitaClassificacaoGeral: async() => {

        let url = `${process.env.REACT_APP_API}/application/calculaClassificacao/Geral`;

        const response = new XMLHttpRequest();
        response.open('GET', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send();
        const dados = JSON.parse(response.responseText);

        return dados;
    },
    uploadFiles: async(file: any, filename: string) => {

        let formData = new FormData();
        formData.append(file, filename);
        let url = `${process.env.REACT_APP_API}/application/upload/File`;
        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader('Access-Control-Allow-Origin', '*');
        response.send(formData);

        
        const dados = JSON.parse(response.responseText);
        console.log(dados);
    },
    realizaEntrega: async(equipe: string, mentor: string, entrega: string, resumo?: string) => {
        
        let url = `${process.env.REACT_APP_API}/application/entregas/create`;

        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({
            "equipe": equipe,
            "mentor": mentor,
            "entrega": entrega,
            "resumo": resumo,
            "tipo": "",
            "nota": 50
        }));

        let dados = JSON.parse(response.responseText);

        return dados;
    },
    pontosExtras: async(equipe: string, entrega: string, nota: string, mentor?: string, resumo?: string) => {
        
        let url = `${process.env.REACT_APP_API}/application/entregas/create`;

        const response = new XMLHttpRequest();
        response.open('POST', url, false);
        response.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        response.send(JSON.stringify({
            "equipe": equipe,
            "mentor": "Sistema",
            "entrega": entrega,
            "resumo": resumo,
            "tipo": "",
            "nota": nota
        }));

        let dados = JSON.parse(response.responseText);

        return dados;
    },
});